html.dark .shiki,
html.dark .shiki span {
  position: relative;
  color: var(--shiki-dark) !important;
  /**
   * TODO: make background color variable as well
   * but make sure line highlighting doesn't break
   * when setting it to dark mode
   */
  /* background-color: var(--shiki-dark-bg) !important; */
  font-style: var(--shiki-dark-font-style) !important;
  font-weight: var(--shiki-dark-font-weight) !important;
  text-decoration: var(--shiki-dark-text-decoration) !important;
}

html.dark,
html:not(.dark) .codeblock-dark {
  /*********************************************************
  * Line highlighting
  */
  pre.shiki > code {
    position: relative;
    z-index: 1;
    display: block;
    width: max-content;
    min-width: 100%;
  }

  .line-highlight {
    background: rgb(var(--primary-light) / 0.2) !important;
    width: 100%;
    display: inline-block;
    position: relative;
    z-index: 0;
  }

  .line-highlight::before,
  .line-highlight::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1.25rem;
    background: rgb(var(--primary-light) / 0.2) !important;
  }

  .line-highlight::before {
    left: -1.25rem;
    border-left: 4px solid rgb(var(--primary-light) / 1);
  }
  .line-highlight::after {
    right: -1.25rem;
  }

  pre[class^='language-diff-'] {
    @apply flex px-9;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }
}

html:not(.dark) .codeblock-light {
  /*********************************************************
  * Line highlighting
  */
  pre.shiki > code {
    position: relative;
    z-index: 1;
    display: block;
    width: max-content;
    min-width: 100%;
  }

  .line-highlight {
    background: rgb(var(--primary-light) / 0.2) !important;
    width: 100%;
    display: inline-block;
    position: relative;
    z-index: 0;
  }

  .line-highlight::before,
  .line-highlight::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1.25rem;
    background: rgb(var(--primary-light) / 0.2) !important;
  }

  .line-highlight::before {
    left: -1.25rem;
    border-left: 4px solid rgb(var(--primary-light) / 1);
  }
  .line-highlight::after {
    right: -1.25rem;
  }

  pre[class^='language-diff-'] {
    @apply flex px-9;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }
}

/* Lines of code outside code blocks. Generated when you use single ticks: `example` */
:not(pre) > code {
  @apply break-words rounded-md px-2 py-0.5 text-gray-600 dark:text-gray-200 dark:border-gray-800 bg-gray-100/50 dark:bg-white/5;
}

:not(pre) > code:before,
:not(pre) > code:after {
  content: none !important;
}
